import React, { useState, useEffect } from 'react';
import Covid from 'components/Covid.js';
import styled from 'styled-components';

const StyledFooter = styled.footer`
  position: fixed;
  bottom: 5px;
  right: 10px;
`;

const Countries = ({ countries, handleChange }) => {

  return (
    <>
      <select onChange={handleChange}>
        <option>World</option>
        {
          countries.map((e, i) => {
            return (
              <option
                key={i}
                value={e.ios3}>
                {e.name}
              </option>
            )
          }
          )
        }
      </select>
    </>
  )
}

const Homepage = () => {

  const [covid, setCovid] = useState({
    confirmed: {
      value: ''
    },
    recovered: {
      value: ''
    },
    deaths: {
      value: ''
    }
  });

  useEffect(() => {
    fetch('https://covid19.mathdro.id/api/')
      .then(res => res.json())
      .then(res => setCovid({
        confirmed: {
          value: res.confirmed.value,
        },
        recovered: {
          value: res.recovered.value,
        },
        deaths: {
          value: res.deaths.value,
        },
      }))
      .catch(err => console.log(err)
      );
  }, [])

  const [countries, setCountries] = useState([])
  const [countryName, setCoutryName] = useState('World');

  useEffect(() => {
    fetch('https://covid19.mathdro.id/api/countries/')
      .then(res => res.json())
      .then(res => setCountries(res.countries)
      )
      .catch(err => console.log(err))
  }, [])

  const handleChange = (e) => {

    fetch(`https://covid19.mathdro.id/api/countries/${e.currentTarget.value}`)
      .then(res => res.json())
      .then(res => setCovid({
        confirmed: {
          value: res.confirmed.value,
        },
        recovered: {
          value: res.recovered.value,
        },
        deaths: {
          value: res.deaths.value,
        },
      }))
      .catch(err => console.log(err))

    setCoutryName(e.currentTarget.value);
  }

  return (
    <>
      <Covid covid={covid} setCovid={setCovid} countryName={countryName} />
      <Countries countries={countries} setCountries={setCountries} handleChange={handleChange} />
      <StyledFooter>
        <p>The data changes dynamically, so it may be out of date when displayed. Data API provided by <a target="blank" rel="noopener" href="https://github.com/mathdroid/covid-19-api">Mathdroid</a>.</p>
      </StyledFooter>
    </>
  )
}

export default Homepage;